import React from "react"
import style from "../assets/scss/index.module.scss";
import classNames from "classnames";
import {library} from "@fortawesome/fontawesome-svg-core";
import {fas} from "@fortawesome/free-solid-svg-icons";
import {fab} from "@fortawesome/free-brands-svg-icons";
import SEO from "../component/seo";
import Img from "gatsby-image";
import {graphql} from "gatsby";
import ResumeExperiences from "../component/resume/experiences";
import ResumeProfile from "../component/resume/profile";
import {Certification} from "../component/about/certification";


const Resume: React.FunctionComponent<any> = (props: any) => {
    library.add(fas, fab);
    const {
        allConfigYaml: {edges: [{node: data}]},
        allMdx: {edges: exps}
    }: any = props.data

    return <>
        <SEO title={"Bruno Jan Resume"} description={props.description}/>
        <section className={classNames(style.gLayoutBoxed, style.gBgiV8)}>
            <main className={classNames(style.container)}>
                <div className={classNames(style.row, style.justifyContentBetween, style.gPt100)}>
                    <div className={classNames(style.col12)}>
                        <div className={classNames(style.gBrdAround, style.gBrdGrayLightV4, style.gPa20, style.gMb40)}>
                            <div className={style.row}>
                                <div className={classNames(style.colMd4)}>
                                    <div className={style.gPy30}>
                                        <Img
                                            className={classNames(style.imgFluid, style.w100)}
                                            fluid={props.data.my.fluid}
                                            fadeIn={true}
                                            alt="Bruno Jan"/>
                                    </div>
                                </div>
                                <div className={classNames(style.colMd8)}>
                                    <ResumeProfile {...data} />
                                    <hr className={classNames(style.gBrdGrayLightV4, style.gMy20)}/>
                                    <Certification {...props.data} />
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className={classNames(style.colLg12)}>
                        <ResumeExperiences exps={exps}/>
                    </div>
                </div>
            </main>
        </section>
    </>
}

export const pageQuery = graphql`
    query {
        my: imageSharp(fluid: {originalName: {eq: "myimage.jpg"}}) {
            fluid(maxWidth: 400) {
                ...GatsbyImageSharpFluid
            }
        }
        csm: imageSharp(fluid: {originalName: {eq: "csm.png"}}) {
            fluid(maxWidth: 400) {
                ...GatsbyImageSharpFluid
            }
        }
        csd: imageSharp(fluid: {originalName: {eq: "csd.png"}}) {
            fluid(maxWidth: 400) {
                ...GatsbyImageSharpFluid
            }
        }
        cspo: imageSharp(fluid: {originalName: {eq: "cspo.png"}}) {
            fluid(maxWidth: 400) {
                ...GatsbyImageSharpFluid
            }
        }
        mvp: imageSharp(fluid: {originalName: {eq: "mvp.png"}}) {
            fluid(maxWidth: 400) {
                ...GatsbyImageSharpFluid
            }
        }
        allConfigYaml {
            edges {
                node {
                    about
                    job_title
                    birthday
                    email
                    education
                    links {
                        title
                        url
                        icon {
                            iconName
                            prefix
                        }
                    }
                    skills {
                        title
                        value
                        color {
                            front
                            back
                            text
                        }
                    }
                }
            }
        }
        allMdx(filter: {frontmatter: {category: {eq: "experiences"}}}, sort: {fields: frontmatter___during, order: DESC}) {
            edges {
                node {
                    frontmatter {
                        company
                        title
                        during
                    }
                    body
                }
            }
        }
    }
`

export default Resume;